import Vue from 'vue';
import VueCompositionAPI, {
/*reactive, */
ref } from '@vue/composition-api';
import { getBookList } from '@/components/ajax-demo/http-request/bookInfoOperations';
Vue.use(VueCompositionAPI);
var toast = new Vue().$toast; // 不能使用 reactive

var rows = ref([{}]);
export var getBookInfo = function getBookInfo() {
  getBookList().then(function (res) {
    if (res.status === 200) {
      var data = res.data.data;
      rows.value = data;
      return rows;
    }
  }).catch(function (err) {
    toast && toast("\u83B7\u53D6\u6570\u636E\u5931\u8D25\uFF01".concat(err.message));
    return "\u83B7\u53D6\u6570\u636E\u5931\u8D25\uFF01".concat(err.message);
  });
  return rows;
};