import axios from 'axios';
axios.defaults.baseURL = 'http://www.liulongbin.top:3006/';
export var addBook = function addBook(paramsData) {
  // 发送添加书本信息请求
  return axios.post('/api/addbook', paramsData, {
    timeout: 0
  });
};
export var delBook = function delBook(paramsData) {
  return axios.get('/api/delbook', {
    params: paramsData
  });
};
export var getBookList = function getBookList() {
  return axios.get('/api/getbooks', {});
};
export var getBook = function getBook(id, bookname) {
  return axios.get('/api/getbooks', {
    params: {
      id: "".concat(id),
      bookname: bookname
    }
  });
};