import "core-js/modules/es.string.trim.js";
import { defineComponent, ref, inject } from '@vue/composition-api';
import VueButton from '@/components/button/VueButton.vue';
import VueInput from '@/components/input/VueInput.vue';
import { addBook } from '@/components/ajax-demo/http-request/bookInfoOperations';
export default defineComponent({
  name: 'AddBookInfo',
  props: {},
  components: {
    VueButton: VueButton,
    VueInput: VueInput
  },
  setup: function setup(props, ctx) {
    var eventbus = inject('eventbus');
    var toast = inject('toast');
    var msgBook = ref('');
    var msgAuthor = ref('');
    var msgPublisher = ref('');

    var addBookInfo = function addBookInfo() {
      msgBook.value = ctx.refs.iptBookname.value.trim();
      msgAuthor.value = ctx.refs.iptAuthor.value.trim();
      msgPublisher.value = ctx.refs.iptPublisher.value.trim();

      if (msgBook.value.length * msgAuthor.value.length * msgPublisher.value.length === 0) {
        return toast && toast('请填写完整的图书信息！');
      } // 发送添加书本信息请求


      addBook({
        bookname: msgBook.value,
        author: msgAuthor.value,
        publisher: msgPublisher.value
      }).then(function (res) {
        var data = res.data;

        if (data.status === 502) {
          return toast && toast(data.msg);
        }

        toast && toast(data.msg); // 通知兄弟组件 ShowBookInfo.vue 更新渲染图书列表

        eventbus === null || eventbus === void 0 ? void 0 : eventbus.$emit('renderBookList'); // 清空表单

        ctx.refs.bookInfoForm.reset();
      });
    };

    return {
      msgBook: msgBook,
      msgAuthor: msgAuthor,
      msgPublisher: msgPublisher,
      addBookInfo: addBookInfo
    };
  }
});