import VueButton from '@/components/button/VueButton.vue';
import VueInput from '@/components/input/VueInput.vue';
import { defineComponent, onMounted, reactive, ref } from '@vue/composition-api';
export default defineComponent({
  name: 'FormData',
  components: {
    VueButton: VueButton,
    VueInput: VueInput
  },
  props: {},
  setup: function setup(props, ctx) {
    var resData = ref({});
    var data = ref({});
    var form = ctx.refs.form1; // 根据 form 表单创建 FormData 对象，会自动将表单数据填充到 FormData 对象中
    // 无需手动 调用 .append 方法

    var formData = reactive(new FormData(form));

    var onSubmit = function onSubmit()
    /*, e: Event*/
    {
      // e.preventDefault(); // 使用 Ajax 的形式提交表单 避险阻止默认提交行为
      formData = new FormData(form); // 无需设置 `xhr.setHeader` 的数据类型

      var xhr = new XMLHttpRequest();
      xhr.open('POST', 'http://www.liulongbin.top:3006/api/formdata');

      xhr.onreadystatechange = function () {
        if (xhr.readyState == 4 && xhr.status == 200) {
          resData.value = JSON.parse(xhr.responseText);
          data.value = resData.value.data;
        }
      };

      xhr.send(formData);
    };

    onMounted(function () {
      form = ctx.refs.form1; // 根据 form 表单创建 FormData 对象，会自动将表单数据填充到 FormData 对象中
      // 无需手动 调用 .append 方法

      formData = new FormData(form);
    });
    return {
      resData: resData,
      onSubmit: onSubmit,
      formData: formData,
      form: form,
      data: data
    };
  }
});